* {
  box-sizing: border-box;
}

@-webkit-keyframes popUp {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes popUp {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
@-webkit-keyframes slideUp {
  from {
    transform: translateY(5px);
    opacity: 0;
  }
  to {
    transform: translateY(0px);
    opacity: 1;
  }
}
@keyframes slideUp {
  from {
    transform: translateY(5px);
    opacity: 0;
  }
  to {
    transform: translateY(0px);
    opacity: 1;
  }
}

.profile {
  -webkit-animation: popUp ease-in-out 350ms;
  animation: popUp ease-in-out 350ms;
  background: #bdbdbd;
  border-radius: 25px;
  box-shadow: 0 0 40px 0px #0000002b;
  margin-top: 40px;
  margin-left: -250px;
  left: 50%;
  position: absolute;
  width: 500px;
}
.profile__account {
  align-self: center;
  display: flex;
  flex: 1 1;
  justify-content: flex-end;
  padding-right: 115px;
}

.profile__header {
  display: flex;
  margin-bottom: 20px;
}
.profile__icon {
  flex: none;
  font-size: 1.5em;
  margin-right: 10px;
  padding-top: 3px;
}
.profile__icon--green {
  color: #009689;
}

.profile__icon--red {
  color: #e61d1d;
}
.profile__icon--blue {
  color: #2196f3;
}
.profile__key {
  font-size: 23px;
  font-weight: 400;
  text-align: center;
}
.profile__picture {
  background: #ffffff;
  border-radius: 100px;
  border: 10px solid #ffffff;
  height: 125px;
  position: absolute;
  left: 40%;
  top: -80px;
  width: 125px;
}
.profile__picture:before {
  border-radius: 100px;
  box-shadow: 0 0 40px 0px #0000002b;
  content: "";
  height: calc(100% + 20px);
  left: -10px;
  position: absolute;
  top: -10px;
  width: calc(100% + 20px);
  z-index: -1;
}
.profile__picture img {
  border-radius: 100px;
  height: 100%;
  width: 100%;
}
.profile__stat {
  -webkit-animation: slideUp ease-in-out 350ms forwards;
  animation: slideUp ease-in-out 350ms forwards;
  border-right: 1px solid #e9e9e9;
  display: flex;
  flex: 1 1;
  justify-content: center;
  opacity: 0;
  padding: 20px 8px;
  transform: translateY(5px);
}
.profile__stat:last-of-type {
  border-right: none;
}
.profile__stat:nth-child(1) {
  -webkit-animation-delay: 400ms;
  animation-delay: 400ms;
}
.profile__stat:nth-child(2) {
  -webkit-animation-delay: 500ms;
  animation-delay: 500ms;
}
.profile__stat:nth-child(3) {
  -webkit-animation-delay: 600ms;
  animation-delay: 600ms;
}
.profile__stats {
  display: flex;
}
.profile__username {
  font-weight: 600;
  text-align: right;
}
.profile__value {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
}

@media only screen and (max-width: 320px) {
  .profile {
    margin-left: -150px;
    left: 50%;
    width: 300px;
  }
  .profile__picture {
    height: 75px;
    left: 35%;
    top: -50px;
    width: 75px;
  }

  .profile__icon {
    margin-left: 30px;
  }

  .profile__username {
    margin-right: 60px;
    margin-top: 20px;
  }

  .profile__header,
  .profile__account {
    width: 100%;
    margin: 0;
    padding: 0;
  }

  .profile__value {
    font-size: 12px;
    font-weight: 500;
  }

  .profile__stat {
    flex-direction: column;
    justify-content: center;
  }
}

