.list {
  margin-right: 40px;
  margin-top: 40px;
  margin-bottom: 40px;
}

.list .listItem {
  list-style: none;
  padding: 18px;
  text-align: center;
  margin-bottom: 3px;
  opacity: 0;
  border-radius: 25px;
  border: #ffffff 1px solid;
}

.list .listItem:nth-child(n) {
  -webkit-animation: pop 1.5s 0.2s forwards;
          animation: pop 1.5s 0.2s forwards;
  background: #2196f3;
  color: #ffffff;
}
@-webkit-keyframes pop {
  0% {
    opacity: 0.5;
    transform: scale(0.5);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes pop {
  0% {
    opacity: 0.5;
    transform: scale(0.5);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

